import React, {
    useEffect,
    useState,
    useCallback,
    useMemo,
} from 'react';
import Toast from '$components/toast';
import axios from '$base/tools/axios';
import Bridge from '$base/tools/bridge'

import Loading from '$components/loading';
import { GIFTS_CONFIG } from '$utils/consts';

import './getship.less';

let buyLock = false;

const GetShip = React.memo(({
    account = 0,
    onSuccess = () => {},
    roomId = 0
}) => {
    const [currentSelect, setCurrentSelect] = useState(0);

    const [giftInfo, setGiftInfo] = useState(GIFTS_CONFIG);

    const gift = useMemo(() => {
        let res;
        if (giftInfo) {
            res = {
                ...giftInfo,
                giftNum: giftInfo.batch[currentSelect].giftNum,
            };
        }
        return res;
    }, [giftInfo, currentSelect]);

    const handleBuy = useCallback(async () => {
        const balance = account;
        if (balance >= gift.worth * gift.giftNum) {
            if (buyLock) {
                return;
            }
            buyLock = true;
            let res = null;
            try {
                res = await axios.post(`/app/lottery-star/buy-ticket.json?ticketNum=${gift.giftNum}&roomId=${roomId}`);
            } catch (e) {
                Toast.fail(e.message || '网络错误请稍后重试');
            }
            if (res.success) {
                Toast.info('购买成功');
                onSuccess();
            } else {
                Toast.fail(res.message || '网络错误请稍后重试');
            }
            buyLock = false;
        } else {
            Toast.fail('饼干不足，请先充值');
            setTimeout(() => {
                Bridge.callmethod(
                    "chargePopup",
                    "",
                    function (data) {
                        Toast.info('购买成功');
                    }
                )
            }, 1000);
        }
    }, [account, gift, onSuccess]);

    if (!giftInfo || giftInfo.length <= 0) {
        return (
            <div className="m-getship">
                <div className="m-getship-loading">
                    <Loading />
                </div>
            </div>
        );
    }

    return (
        <div className="m-getship">
            <div className="m-getship-header">
                <div
                    className="m-getship-header-icon"
                    style={{
                        backgroundImage: `url(${gift.iconUrl})`
                    }} />
                <div className="m-getship-header-desc">
                    <div className="m-getship-header-desc-title">
                        赠送
                        {gift.name}
                    </div>
                    <div className="m-getship-header-desc-subtitle">
                        <p>
                            每赠送1个
                            {gift.name}
                            给主播，可获得飞船+1
                        </p>
                    </div>
                </div>
            </div>
            <div className="m-getship-selects">
                {
                    giftInfo.batch.map((v, index) => (
                        <div
                            onClick={() => {
                                setCurrentSelect(index);
                            }}
                            key={index}
                            className={`m-getship-select ${index !== currentSelect ? 'm-getship-select--disable' : ''}`}>
                            {v.giftNum}
                            个
                        </div>
                    ))
                }
            </div>
            <div className="m-getship-count">
                需
                {gift.worth * gift.giftNum}
                波币
            </div>
            <div className="m-getship-footer">
                <div className="m-getship-footer-count">
                    余额：{account}
                </div>
                <div
                    onClick={handleBuy}
                    className="m-getship-footer-button">
                    购买
                </div>
            </div>
        </div>
    );
});

export default GetShip;
