import React, { useEffect, useState, useCallback } from 'react';
import withBatch from '$base/tools/withBatch';
import './notify.less';

const Notify = React.memo(({
    className = '',
    texts = [],
}) => {
    const [count, setCount] = useState(0);
    const [animate, setAnimate] = useState(true);
    useEffect(() => {
        const timer = setInterval(() => {
            setCount((v) => {
                if (v >= texts.length - 1) {
                    return 0;
                }
                return v + 1;
            });
        }, 3000);
        return () => {
            clearTimeout(timer);
        };
    }, [texts.length]);
    const handleDisableAnimation = useCallback(() => {
        if (count === texts.length - 1) {
            withBatch(() => {
                setAnimate(false);
                setCount(0);
            });
            setTimeout(() => {
                setAnimate(true);
            }, 10);
        }
    }, [count, texts.length]);

    const percent = (100 / texts.length);

    return (
        <div className={`m-notify ${className}`}>
            <div
                className="m-notify-list"
                style={{
                    transition: animate ? 'transform 1s ease' : '',
                    transform: `translateY(-${count * percent}%)`,
                }}
                onTransitionEnd={handleDisableAnimation}>
                {
                    texts.map((v, index) => (
                        <div
                            className="m-notify-item"
                            key={index}
                            dangerouslySetInnerHTML={{ __html: v }}
                        >
                        </div>
                    ))
                }
            </div>
        </div>
    );
});

export default Notify;
