// 测试环境请求接口host: https://prod-lottery.qiuchapp.com
import React, {
    useState,
    useCallback,
    useEffect,
    useRef,
    useMemo
} from 'react';
import 'fastclick';
import axios from '$base/tools/axios';
import cookie from '$base/tools/cookie';
import Toast from '$components/toast';
import Modal from '$components/modal';
import Number from '$components/number';
import Planet from '$components/planet';
import Timeout from '$components/timeout';
import GetShip from '$components/getship';
import Notify from '$components/notify';
import Announce from '$components/announce';
import SendShip from '$components/sendship';
import withBatch from '$base/tools/withBatch';
import { parse } from '$utils/parse';
import kh2 from '$base/imgs/kh2.png';
import kh1 from '$base/imgs/kh1.png';
import gth from '$base/imgs/gth.png'

import {
    PLANETS
} from '$utils/consts';

import '$base/common_style/base.scss';
import './index.less';

const params = parse(window.location.search);


// 如果有roomId,先把roomId放进cookie里
if (params?.roomId){
	cookie.set('roomId', params?.roomId)
}


function Home() {
    // 超时弹窗
    const [timeoutModalVisible, setTimeoutModalVisible] = useState(false);

    const showTimeoutModal = useCallback(() => {
        setTimeoutModalVisible(true);
    }, []);

    const hideTimeoutModal = useCallback(() => {
        setTimeoutModalVisible(false);
    }, []);

    // 增减ship弹窗
    const [shipModalVisible, setShipModalVisible] = useState(false);
    const hideShipModal = useCallback(() => {
        setShipModalVisible(false);
    }, []);

    // 当前服务器时间、开奖情况及用户获奖信息
    const [mainInfo, setMainInfo] = useState(null);

    // 各星球信息及派遣情况
    const [shipsInfo, setShipsInfo] = useState(null);
    // 获取用户拥有飞船数量
    const [shipAmount, setShipAmount] = useState(0);
    // 获取用户拥有饼干余额
    const [account, setAccount] = useState(0);

    const getBalance = useCallback(async () => {
        try {
            await axios({
                url: '/app/lottery-star/ticket-info.json',
                method: 'get'
            }).then((response) => {
                const {
                    data: {
                        gold = 0,
                        ticketNum = 0
                    } = {}
                } = response;
                // 剩余飞船数量
                setShipAmount(ticketNum);
                // 剩余波币数量
                setAccount(gold);
            })
        } catch (err) {
            Toast.fail(e.message || '网络错误，请稍后重试');
        }
    }, [])

    const getShipsData = useCallback(async () => {
        let res = null;
        try {
            await axios({
                url: '/app/lottery-star/cur-commit-order.json',
                method: 'get'
            }).then(r => {
                res = r?.data;
            }).catch(e => {
                Toast.fail(e.message || '网络错误，请稍后重试');
            });
        } catch (e) {
            Toast.fail(e.message || '网络错误，请稍后重试');
            return;
        }
        const sites = res.items;
        if (sites && sites.length === Object.keys(PLANETS).length) {
            sites.sort((a, b) => a.confId - b.confId);
            const info = sites.map((v, i) => ({
                // codeKey: v.codeKey,
                starCode: v.code, // 星球代号
                mulPower: v.rate, // 星球收益倍率
                myConfirm: v.myOrderNum || 0, // 该星球当前确认派遣的数目
                name: v.name, // 星球名称
                currentNum: 0, // 每次拉取接口后，都清零当前选择数量
            }));
            setShipsInfo(info);
        } else {
            console.log('接口返回数据出错');
        }
    }, []);
    
    const getShipInfo = useCallback(() => {
        getShipsData();
        getBalance();
    }, [getShipsData, getBalance]);

    useEffect(() => {
        getShipInfo();
    }, [getShipInfo]);

    // 探险倒计时
    const [count, setCount] = useState(60);
    const countRef = useRef(60);

    // 开奖时，是否需要打开结果页
    const showResult = useRef(true);

    // 购买飞船弹窗
    const [buyModalVisible, setBuyModalVisible] = useState(false);

    const hideBuyModal = useCallback(() => {
        // 隐藏飞船弹窗时，更新剩余飞船和音符
        setTimeout(() => {
            // 延迟一秒拉取飞船数量，服务端更新飞船数目比较慢
            getShipInfo();
        }, 1000);
        // 隐藏购买弹层
        setBuyModalVisible(false);
    }, [getShipInfo]);

    // 展示购买飞船弹窗
    const showBuyModal = useCallback(() => {
        setBuyModalVisible(true);
    }, []);

    // 计算当前待派遣飞船总数目
    const totalCounts = useMemo(() => {
        if (shipsInfo) {
            let sum = 0;
            shipsInfo.forEach((ship) => {
                sum += ship.currentNum;
            });
            return sum;
        }
        return 0;
    }, [shipsInfo]);

    // 手动更新当前飞船派遣数量
    const handleCurrentShipsInfo = useCallback((shipInfo) => {
        if (shipInfo.targetNum !== shipInfo.currentNum) {
            setShipsInfo((info) => {
                const res = info.map((item) => {
                    if (item.starCode === shipInfo.starCode) {
                        return {
                            ...item,
                            currentNum: shipInfo.targetNum, // 更新飞船待派遣数量
                        };
                    }
                    return {
                        ...item,
                    };
                });
                console.log('手动选择后的下注信息', res)
                return res;
            });
        }
        // 隐藏派遣飞船弹窗
        hideShipModal();
    }, [hideShipModal]);

    // 确认派遣
    // 判断是否处于派遣中
    const [sending, setSending] = useState(false);

    const confirmSend = useCallback(() => {
        let timer = null;
        async function send() {
            // 待派遣数量大于0
            if (totalCounts > 0) {
                // 飞船余额大于待派遣数量
                if (shipAmount >= totalCounts) {
                    let res = null;
                    try {
                        // TODO: 待调试派遣接口
                        res = await axios.post('/app/lottery-star/commit-order.json', {
                            roomId: params?.roomId,
                            "items": shipsInfo.map((v) => ({
                                "starTypeCode": v.starCode,
                                "num": v.currentNum 
                            }))
                        });
                    } catch (e) {
                        Toast.fail(e.message || '网络错误请稍后重试');
                    }
                    // 标记派遣中为false
                    if (res.success) {
                        Toast.info('飞船派遣成功，等待起航');
                        // 更新派遣信息
                        await getShipInfo();
                        setSending(false);
                    } else {
                        Toast.fail(res.message || '网络错误请稍后重试');
                    }
                } else {
                    // 展示飞船购买弹窗
                    showBuyModal();
                    // 标记派遣中为false
                    setSending(false);
                }
            } else {
                // 待派遣数量为0，则标记派遣中为false
                setSending(false);
            }
        }
        // 派遣中，无操作
        if (sending) return;
        setSending(true);
        send();
    }, [getShipInfo, sending, totalCounts, showBuyModal, shipAmount, shipsInfo, getBalance]);


    // 当前选中星球的飞船派遣信息，用于飞船选择弹窗使用，不做数据暂存
    const [currentShipInfo, setCurrentShipInfo] = useState({});

    const showSelect = useCallback((planetInfo) => {
        // 倒计时大于10s，才可以修改飞船数量
        if (countRef.current > 10) {
            // 设置当前选中飞船基础信息
            setCurrentShipInfo(planetInfo);
            // 展示飞船派遣弹窗
            setShipModalVisible(true);
        }
    }, []);

    // 记录开奖结果信息
    const [result, setResult] = useState(null);
    // 记录是否处于动画播放完，结果还没收到
    const [opening, setOpening] = useState(false);
    // 获奖结果通过url参数带过去result页面
    const handleOpenResult = useCallback((data) => {
        // 如果已经开奖，且允许跳转结果页，则跳转结果页
        if (data.lottery && showResult.current) {
            const location = window.location;
            const pathname = location.pathname;
            let url;
            if (pathname.includes('home.html')) {
                url = `${location.origin}${pathname.replace('home.html', 'result.html')}`.split('?')[0];
            } else {
                url = `${location.origin}${`${pathname}/raceresult.html`}`.split('?')[0];
            }
            window.location.href = `${url}?result=${encodeURIComponent(JSON.stringify(data))}&roomId=${params?.roomId}`;
        }
    }, []);

    // 动画播放结束
    const handleShipAnimationEnd = useCallback(() => {
        if (result.lottery) {
            // 有结果，就打开结果页
            handleOpenResult({
                ...result,
                count,
            });
        } else {
            // 否则，等待，展示loading
            setOpening(true);
        }
    }, [result, handleOpenResult, count]);


    // 标记是否轮询
    const end = useRef(false);

    // 轮询获取当前轮时间和中奖信息
    useEffect(() => {
        let timer;
        async function getTime() {
            // 结束后停止轮询
            if (end.current) return;
            let res = null;
            try {
                // TODO: 调试结果接口
                // res = RESULTINFO;
                const data = await axios('/app/lottery-star/cur-session.json');
                res = data?.data || {};
            } catch (e) {
                console.log(e);
            }
            if (res) {
                console.log(res, '本轮结果');
                withBatch(() => {
                    // 轮询未结束，才做更新
                    if (!end.current) {
                        setMainInfo(res);
                        const currentTime = 60 - new Date(res.currentServerTs).getSeconds();
                        // const currentTime = 12;
                        // const currentTime = 60;
                        setCount(currentTime);
                        // 动画播放中，则暂存结果信息
                        if (!opening) {
                            setResult({
                                num: res?.sessionResult?.myNum || 0,
                                starCode: res?.resultStarCode || '',
                                myResult: res?.sessionResult?.rewards || [],
                                lottery: res?.lottery,
                                winningUserCount: res?.sessionResult?.hitNum
                            });
                        } else {
                            // 否则，直接打开结果页
                            handleOpenResult({
                                num: res?.sessionResult?.myNum || 0,
                                starCode: res?.resultStarCode || '',
                                myResult: res?.sessionResult?.rewards || [],
                                lottery: res.lottery,
                                winningUserCount: res?.sessionResult?.hitNum,
                                count: currentTime,
                            });
                        }
                    }
                });
            }
            timer = setTimeout(() => {
                getTime();
            }, 4000);
        }
        if (shipsInfo) {
            getTime();
        }
        return () => {
            clearTimeout(timer);
        };
    }, [handleOpenResult, opening, shipsInfo]);

    // 倒计时减1逻辑
    useEffect(() => {
        const timer = setInterval(() => {
            setCount((v) => {
                if (v - 1 <= 0) {
                    // end.current = true; // 标记本场已结束，停止接口轮询及数据更新
                    // showTimeoutModal(); // 展示超时弹窗
                    window.location.reload();
                    setOpening(false); // 隐藏loading弹窗
                    return 0;
                }
                return v - 1;
            });
        }, 1000);
        return () => {
            clearInterval(timer);
        };
    }, [mainInfo, showTimeoutModal]);

    useEffect(() => {
        async function getTime() {
            let res = null;
            try {
                // TODO: 调试结果接口
                // res = RESULTINFO;
                const data = await axios('/app/lottery-star/cur-session.json');
                res = data?.data || {};
            } catch (e) {
                console.log(e);
            }
            if (res) {
                handleOpenResult({
                    num: res?.sessionResult?.myNum || 0,
                    starCode: res?.resultStarCode || '',
                    myResult: res?.sessionResult?.rewards || [],
                    count: 0,
                    lottery: res.lottery,
                    winningUserCount: res?.sessionResult?.hitNum
                });
            } else {
                showTimeoutModal();
            }
        }
        // 倒计时到1，获取结果信息
        if (count === 1) {
            getTime();
        }
    }, [count, showTimeoutModal, handleOpenResult]);

    // 去榜单
    const openUrl = useCallback((page) => {
        const location = window.location;
        const pathname = location.pathname;
        let url;
        if (pathname.includes('racehome.html')) {
            url = `${location.origin}${pathname.replace('racehome.html', `${page}.html`)}`.split('?')[0];
        } else {
            url = `${location.origin}${`${pathname}/${page}.html`}`.split('?')[0];;
        }
        window.location.href = `${url}?roomId=${params?.roomId}`;
    }, []);

    // 初始化滚动区域
    const plantScrollRef = useRef(null);
    const scrolled = useRef(false);
    useEffect(() => {
        if (plantScrollRef.current && !scrolled.current) {
            const el = plantScrollRef.current;
            el.scrollLeft = el.offsetWidth * 0.071;
            scrolled.current = true;
        }
    });

    console.log(shipsInfo, 'shipsInfo', mainInfo)
    // 星球及飞船派遣情况未获取，不展示信息
    if (!shipsInfo || !mainInfo) {
        return (
            <div className='m-main' />
        );
    }

    return (
        <div className="m-main">
            <div className="m-main-howplay" onClick={() => {openUrl('rule');}}>规则</div>
            <div className="m-main-rank" onClick={() => {openUrl('rank');}} />
            <div className="m-main-record" onClick={() => {openUrl('record');}}/>
            <Announce shipsInfo={shipsInfo} />
            {
                count > 10 ? (
                    <div className="m-main-countdown">
                        <img
                            className="m-main-countdown-left"
                            src={kh2} />
                        <div className="m-main-countdown-num">
                            <Number num="0" />
                            <Number num="0" />
                            <Number num="colon" />
                            <Number num={Math.floor((count - 10) / 10)} />
                            <Number num={(count - 10) % 10} />
                        </div>
                        <img
                            className="m-main-countdown-right"
                            src={kh1} />
                    </div>
                ) : (
                    <div className="m-main-countdown">
                        <div className="m-main-countdown-desc">
                            正在探险中
                        </div>
                        <div className="m-main-countdown-num m-main-countdown-num--margin">
                            <Number num={Math.floor(count / 10)} className="m-main-countdown-num-small" />
                            <Number num={count % 10} className="m-main-countdown-num-small" />
                        </div>
                        <div className="m-main-countdown-second">
                            s
                        </div>
                    </div>
                )
            }
            <div className="m-main-notify">
                <img
                    className="m-main-notify-icon"
                    src={gth} />
                <Notify
                    texts={[
                        '尽快选择目标星球派遣飞船',
                        '选多个星球探险成功率更高',
                        '尽快选择目标星球派遣飞船'
                    ]} />
                <img
                    className="m-main-notify-icon"
                    src={gth} />
            </div>
            {/* 玩法区域 */}
            <div
                className="m-main-planets-wrapper"
                ref={plantScrollRef}>
                <div className="m-main-planets">
                    {
                        shipsInfo.map((v, index) => (
                            <Planet
                                showSelect={showSelect}
                                starCode={v.starCode}
                                key={v.starCode}
                                mulPower={v.mulPower}
                                myConfirm={v.myConfirm}
                                currentNum={v.currentNum} />
                        ))
                    }
                </div>
            </div>
            {
                count > 10 && (
                    <div
                        className="m-main-bottom">
                        <div className="m-main-bottom-left">{shipAmount}</div>
                        <div className="m-main-bottom-get" onClick={showBuyModal} />
                        <div
                            onClick={confirmSend}
                            className={`m-main-bottom-send ${totalCounts === 0 || sending ? 'm-main-bottom-send--disable' : ''}`}>
                            {totalCounts}
                        </div>
                    </div>
                )
            }
            {
                count <= 10 && (
                    <div className="m-main-loading">
                        <div
                            onAnimationEnd={handleShipAnimationEnd}
                            className="m-main-loading-ship" />
                    </div>
                )
            }
            <Modal
                onClose={hideTimeoutModal}
                className="m-main-modal"
                visiable={timeoutModalVisible}>
                <Timeout />
            </Modal>
            <Modal
                onClose={hideShipModal}
                showType="bottom"
                visiable={shipModalVisible}
                className="m-main-ship-modal m-main-modal">
                <SendShip
                    onConfirm={handleCurrentShipsInfo}
                    shipInfo={currentShipInfo} />
            </Modal>
            <Modal
                onClose={hideBuyModal}
                showType="bottom"
                className="m-main-buy-modal"
                visiable={buyModalVisible}>
                <GetShip
                    roomId={params?.roomId}
                    onSuccess={hideBuyModal}
                    account={account} />
            </Modal>
        </div>
    );
}

export default Home;


