import React, { useCallback, useState } from 'react';
import Toast from '$components/toast';
import { PLANETS } from '$utils/consts';

import './sendship.less';

const steps = [1, 10, 50];

const Limit = 50;

const SendShip = React.memo(({
    shipInfo = {
        starCode: '',
        mulPower: '',
        myConfirm: '',
        currentNum: '',
        name: '',
    },
    onConfirm = () => {},
}) => {
    const planetIcon = PLANETS[shipInfo.starCode] && PLANETS[shipInfo.starCode].icon;
    const [activeStep, setActiveStep] = useState(1);
    const [tempNum, setTempNum] = useState(shipInfo.currentNum);
    const handleAdd = useCallback(() => {
        const sum = tempNum + activeStep + shipInfo.myConfirm;
        if (sum > Limit) {
            Toast.info(`每个星球最多派遣${Limit}架哦`);
            return setTempNum(Limit - shipInfo.myConfirm);
        }
        return setTempNum(tempNum + activeStep);
    }, [activeStep, shipInfo.myConfirm, tempNum]);
    const handleMinus = useCallback(() => {
        setTempNum((v) => {
            const res = v - activeStep;
            if (res < 0) {
                return 0;
            }
            return res;
        });
    }, [activeStep]);
    const handleConfirm = useCallback(() => {
        onConfirm({
            ...shipInfo,
            targetNum: tempNum,
        });
    }, [onConfirm, shipInfo, tempNum]);
    return (
        <div className="m-sendship">
            <div className="m-sendship-content">
                <div
                    className="m-sendship-planet"
                    style={{
                        backgroundImage: `url(${planetIcon})`
                    }}>
                    <div className="m-sendship-planet-num">{shipInfo.mulPower}</div>
                </div>
                <div className="m-sendship-send">
                    <div className="m-sendship-send-num">
                        选择派遣架数
                    </div>
                    <div className="m-sendship-send-operate">
                        <div className="m-sendship-send-operate-hotarea" onClick={handleMinus}>
                            <div
                                className={`m-sendship-send-operate-minus ${tempNum <= 0 ? 'm-sendship-send-operate-minus--disable' : ''}`} />
                        </div>
                        <div className="m-sendship-send-operate-number">
                            {tempNum}
                        </div>
                        <div className="m-sendship-send-operate-hotarea" onClick={handleAdd}>
                            <div
                                className={`m-sendship-send-operate-add ${tempNum + shipInfo.myConfirm >= 50 ? 'm-sendship-send-operate-add--disable' : ''}`} />
                        </div>
                    </div>
                </div>
            </div>
            <div className="m-sendship-confirm" onClick={handleConfirm} />
            <div className="m-sendship-select">
                输入倍数：
            </div>
            <div className="m-sendship-steps">
                {
                    steps.map((v) => (
                        <div
                            onClick={() => {
                                setActiveStep(v);
                            }}
                            key={v}
                            className={`m-sendship-step ${v === activeStep ? '' : 'm-sendship-step--disable'}`}>
                            {v}
                            架
                        </div>
                    ))
                }
            </div>
            <div className="m-sendship-close" />
        </div>
    );
});

export default SendShip;
