import React, { useCallback } from 'react';
import './timeout.less';

const Timeout = React.memo(() => {
    const handleClick = useCallback(() => {
        window.location.reload();
    }, []);
    return (
        <div className="m-timeout">
            <div
                className="m-timeout-button"
                onClick={handleClick} />
        </div>
    );
});

export default Timeout;
