import axios from 'axios'
import Bridge from './bridge'

// 全局注入token到localStorage
Bridge.callmethod("getToken", "getToken", function (token) {
  localStorage.setItem("token", token);
});

let baseURL = 'https://portal.MvvmDemo.live'; // 线上
if (location.host.includes('localhost:3000')) {
    baseURL = 'https://test-hl.jingyinnet.com'; // 本地
} else if (location.host.includes('test-kepler.jingyinnet.com')) {
  baseURL = 'https://test-hl.jingyinnet.com'; // 测试环境
}
// import { getCurrentInstance } from 'vue'

// console.log('process.env.VUE_APP_BASE_URL', process.env.VUE_APP_BASE_URL)
// alert('VUE_APP_BASE_URL: ' + process.env.VUE_APP_BASE_URL)

// 创建一个 axios 实例
const service = axios.create({
  baseURL, // 所有的请求地址前缀部分
  timeout: 60000, // 请求超时时间毫秒
  withCredentials: true, // 异步请求携带cookie
  headers: {
    // 设置后端需要的传参类型
    // 'Content-Type': 'application/json',
    // Authorization: 'Bearer eyJ0eXAiOiJKV1QiLCJ0eXBlIjoiSldUIiwiYWxnIjoiSFMyNTYifQ.eyJleHAiOjE2ODkxNzQyNjgsInVzZXJJZCI6IjI4OTMiLCJpYXQiOjE2ODgzMTAyNjh9.F4E5lZMz0QlvfeN6972ic_jNztmdpVnnL-VHOmmFs7Q'
    // 'X-Requested-With': 'XMLHttpRequest'
  }
})

// 添加请求拦截器
service.interceptors.request.use(
  function (config) {
    // 在发送请求之前做些什么
    const token = localStorage.getItem('token')
    token && (config.headers.Authorization = token)
    // config.headers.Authorization = 'BBearer eyJ0eXAiOiJKV1QiLCJ0eXBlIjoiSldUIiwiYWxnIjoiSFMyNTYifQ.eyJleHAiOjE2ODkxNzQyNjgsInVzZXJJZCI6IjI4OTMiLCJpYXQiOjE2ODgzMTAyNjh9.F4E5lZMz0QlvfeN6972ic_jNztmdpVnnL-VHOmmFs7Q'
    return config
  },
  function (error) {
    // 对请求错误做些什么
    console.log(error)
    return Promise.reject(error)
  }
)

// 添加响应拦截器
service.interceptors.response.use(
  function (response) {
    // console.log(response)
    // 2xx 范围内的状态码都会触发该函数。
    // 对响应数据做点什么
    // dataAxios 是 axios 返回数据中的 data
    const dataAxios = response.data
    // 这个状态码是和后端约定的
    // const code = dataAxios.reset
    return dataAxios
  },
  function (error) {
    // 超出 2xx 范围的状态码都会触发该函数。
    // 对响应错误做点什么
    console.log(error)
    return Promise.reject(error)
  }
)

export default service

