import React, { useCallback } from 'react';
import cx from 'classnames';
import * as ReactDOM from 'react-dom'

import './index.less';

const Modal = ({
    visiable,
    children,
    onClose,
    showType='center' // bottom, center, top
}) => {
    if(!visiable) return null;
    
    const closeHandle = useCallback((e) => {
        if(e.target.className === 'modal-wrap') {
            onClose();
        }
    }, [onClose]);

    return (
        ReactDOM.createPortal(
            <div className="modal-wrap" onClick={closeHandle}>
                <div className={cx("modal-content", {
                    center: showType === 'center',
                    bottom: showType === 'bottom',
                    top: showType === 'top',
                    fadein: showType === 'fadein'
                })}>
                    {
                        children
                    }
                </div>
            </div>,
            document.getElementById('root')
        )
    )
};

export default Modal;