import React, { useCallback } from 'react';
import { PLANETS } from '$utils/consts';
import './planet.less';

const Planet = React.memo(({
    starCode = '',
    mulPower = 0,
    myConfirm = 0,
    currentNum = 0,
    showSelect = () => {},
}) => {
    const planet = (PLANETS[starCode] && PLANETS[starCode].name) || '';
    const handleClick = useCallback(() => {
        showSelect({
            starCode, // 当前星球的code
            mulPower, // 当前星球的赔率
            myConfirm, // 我已经下注的数量
            currentNum, // 我已选择的数量
        });
    }, [showSelect, starCode, mulPower, myConfirm, currentNum]);
    if (!planet) return null;
    return (
        <React.Fragment>
            <div className={`m-planet m-planet-${planet}`}>
                <div className="m-planet-main">
                    <div className="m-planet-times">{mulPower}</div>
                </div>
                <div className="m-planet-total">
                    <div className="m-planet-total-textwrapper">
                        <span className="m-planet-total-num">{myConfirm}</span>
                        <span className="m-planet-total-unit">架</span>
                    </div>
                </div>
                <div className="m-planet-button">
                    <span className="m-planet-button-textwrapper">
                        <span className="m-planet-button-num">{currentNum}</span>
                        <span className="m-planet-button-unit">架</span>
                    </span>
                    <span className="m-planet-button-add">+</span>
                </div>
            </div>
            <div
                onClick={handleClick}
                className={`m-planet-hotarea m-planet-hotarea-${planet}`} />
        </React.Fragment>
    );
});

export default Planet;
