import React, { useCallback, useEffect, useState } from 'react';
import Notify from '$components/notify';
import axios from '$base/tools/axios';
// import Fetch from '$utils/fetch';
// import { annountListUrl } from '$service';

import './announce.less';

const Announce = React.memo(({
    shipsInfo
}) => {
    const [list, setList] = useState([]);

    const giftDetail = useCallback((list) => {
        const detail = list.map((v, i) => {
            return ` ${v.rewardNum}个${v.stockName}`
        });
        return detail.join('');
    }, []);

    const getList = useCallback(async () => {
        if (shipsInfo) {
            const data = await axios('/app/lottery-star/hit-broadcast.json');
            const res = data?.data?.items || [];
            const texts = res.map((v, i) => {
                return `${v.username}探得${giftDetail(v.rewards)}`
            });
            texts.push([texts[0]]);
            setList(texts);
        }
    }, [shipsInfo])

    useEffect(() => {
        getList();
    }, [getList]);

    if (!list.length) {
        return null;
    }

    return (
        <div className="m-announce">
            <Notify
                texts={list} />
        </div>
    );
});

export default Announce;